import React from "react";
import styled from "styled-components";
import { Skeleton, Modal, Row, message } from "antd";
import { customMedia } from "../common/GlobalStyles";

import SmallTag from "../common/SmallTag";
import Button from "../common/Button";
import unfilledHeart from "../../images/icons/unfilled_heart.png";
import filledHeart from "../../images/icons/filled_heart.png";

const InfoBox = (props) => {
	const tags = props.club.tags.split(", ");

	return (
		<InfoBoxContainer>
			<ClubThumbnail>
				{props.club.imgUrl ? (
					<img src={props.club.imgUrl} alt="Club Thumbnail" />
				) : (
					<SkeletonImg />
				)}
			</ClubThumbnail>
			<ClubInfo>
				<Title>{props.club.title}</Title>
				<InfoRow>
					<SubTitle> </SubTitle>{" "}
					<Text>
						{props.club.minPersonnel} ~ {props.club.maxPersonnel}
					</Text>
				</InfoRow>
				<InfoRow>
					<SubTitle> </SubTitle>{" "}
					<Text>
						{props.club.startDate} ~ {props.club.endDate}
					</Text>
				</InfoRow>
				<TagContainer>
					{tags.map((tag, i) => (
						<Tag key={i}>{tag}</Tag>
					))}
				</TagContainer>
				<BtnRow>
					<LikeIconContainer>
						<LikeIcon
							onClick={() => {
								props.like
									? props.handleDeleteLike(props.club.id)
									: props.handlePostLike(props.club.id);
							}}
						>
							{props.like === props.club.id ? (
								<img src={filledHeart} alt="Filled like icon"></img>
							) : (
								<img src={unfilledHeart} alt="Unfilled like icon" />
							)}
						</LikeIcon>
					</LikeIconContainer>
					{(() => {
						if (props.club.clubStatus !== "EXPIRED") {
							if (props.userId && props.apply.includes(props.club.id))
								return (
									<>
										<ApplyBtn
											onClick={() => {
												props.handleDeleteApply(props.club.id);
												props.showModal();
											}}
										>
											
										</ApplyBtn>
										<StyledModal
											visible={props.isModalVisible}
											onCancel={() => props.handleCancel()}
										>
											<ModalTitle> .</ModalTitle>
											<ButtonRow>
												<FilledBtn onClick={() => props.handleCancel()}>
													
												</FilledBtn>
											</ButtonRow>
										</StyledModal>
									</>
								);
							else
								return (
									<>
										<ApplyBtn
											onClick={() => {
												if (props.userId) {
													props.handlePostApply(props.club.id);
													props.showModal();
												} else {
													message.error("  .");
												}
											}}
										>
											
										</ApplyBtn>
										<StyledModal
											visible={props.isModalVisible}
											onCancel={() => props.handleCancel()}
										>
											<ModalTitle> .</ModalTitle>
											<ButtonRow>
												<FilledBtn onClick={() => props.handleCancel()}>
													
												</FilledBtn>
											</ButtonRow>
										</StyledModal>
									</>
								);
						} else return <ApplyBtn disabled></ApplyBtn>;
					})()}
				</BtnRow>
			</ClubInfo>
		</InfoBoxContainer>
	);
};

export default InfoBox;

const InfoBoxContainer = styled.div`
	width: 100%;
	height: 320px;
	border: 1.5px solid #e5e5e5;
	border-radius: 10px;

	display: flex;

	${customMedia.lessThan("desktop")`
  height: 280px;
  `}
`;

const ClubThumbnail = styled.div`
	width: 493px;
	height: 100%;

	img {
		width: 100%;
		height: 100%;
		border-radius: 10px 0 0 10px;
	}

	${customMedia.lessThan("desktop")`
  width: 388px;
  `}
`;

const ClubInfo = styled.div`
	width: 50%;
	padding: 0 50px;

	display: flex;
	flex-direction: column;
	justify-content: center;

	${customMedia.lessThan("desktop")`
  padding: 0 35px;
  `}
`;

const InfoRow = styled.div`
	display: flex;
	align-items: center;
	gap: 20px;
`;

const Title = styled.div`
	font-size: 28px;
	font-weight: bold;
	margin-bottom: 16px;

	${customMedia.lessThan("desktop")`
  font-size: 24px;
  `}
`;

const SubTitle = styled.div`
	font-size: 22px;
	font-weight: 500;
	color: #f98404;

	${customMedia.lessThan("desktop")`
  font-size: 18px;
  `}
`;

const Text = styled.div`
	font-size: 20px;

	${customMedia.lessThan("desktop")`
  font-size: 16px;
  `}
`;

const TagContainer = styled.div`
	display: flex;
	gap: 10px;

	margin-top: 15px;
`;

const Tag = styled(SmallTag)`
	& {
		font-size: 16px;
		padding: 7px 25px;

		${customMedia.lessThan("desktop")`
    font-size: 14px;
    padding: 7px 18px;
  `}
	}
`;

const BtnRow = styled.div`
	display: flex;
	gap: 30px;
	margin-top: 40px;
`;

const LikeIconContainer = styled.div`
	width: 50px;
	height: 50px;
	border: 2px solid #e5e5e5;
	border-radius: 5px;

	display: flex;
	justify-content: center;
	align-items: center;
`;

const LikeIcon = styled.div`
	width: 32px;
	height: 32px;
	cursor: pointer;

	${customMedia.lessThan("desktop")`
  width: 28px;
  height: 28px;
  `}

	img {
		width: 100%;
	}
`;

const ApplyBtn = styled(Button)`
	width: 300px;
	height: 50px;
	color: #ffffff;
	background-color: #ff6701;
	border-radius: 5px;
	padding: 0;
	text-align: center;

	${customMedia.lessThan("desktop")`
  width: 250px;
  `}

	&:disabled {
		opacity: 60%;
		cursor: not-allowed;
	}
`;

const StyledModal = styled(Modal)`
	display: flex;
	justify-content: center;

	.ant-modal-content {
		padding: 30px 55px;
		display: flex;
		align-items: center;
	}

	.ant-modal-body {
		text-align: center;
	}

	.ant-modal-footer {
		display: none;
	}
`;

const ModalTitle = styled.div`
	font-size: 20px;
	font-weight: bold;
	margin-bottom: 10px;
`;

const ButtonRow = styled(Row)`
	margin-top: 30px;
	display: flex;
	justify-content: center;
	gap: 50px;
`;

const FilledBtn = styled(Button)`
	& {
		color: #ffffff;
		background-color: #ff6701;
		border: none;
		border-radius: 6px;
		outline: none;
		cursor: pointer;
	}
`;

const SkeletonImg = styled(Skeleton.Image)`
	.ant-skeleton-image {
		width: 493px;
		height: 320px;
	}
`;
