import React, { useState, useEffect } from "react";
import axios from "axios";
import styled from "styled-components";
import { Row, Col, Checkbox, Select, message } from "antd";
import { customMedia } from "../common/GlobalStyles";

import TagFilter from "./TagFilter";
import SearchBar from "./SearchBar";
import ClubCard from "./ClubCard";
import Pagination from "../common/Pagination";
import Spin from "../common/Spin";

const Main = () => {
	const [clubs, setClubs] = useState();
	const [sortBy, setSortBy] = useState("createdAt");
	const [clubStatus, setClubStatus] = useState("");
	const [selectedTags, setSelectedTags] = useState([]);
	const [keyword, setKeyword] = useState("");
	const [likedClubs, setLikedClubs] = useState([]);
	const [total, setTotal] = useState(0);
	const [page, setPage] = useState(1);
	const [loading, setLoading] = useState(true);
	const userId = localStorage.getItem("user_id");

	useEffect(() => {
		fetchData();
		setLoading(false);
	}, [clubStatus, page, keyword, selectedTags, sortBy, userId]);

	const fetchData = async () => {
		const sendTags = selectedTags.join(", ");

		try {
			const res = await axios.get("/clubs", {
				params: {
					sortBy: sortBy,
					tags: sendTags,
					clubStatus: clubStatus,
					keyword: keyword,
					page: page,
				},
			});

			setClubs(res.data.clubList);
			setTotal(res.data.totalCount);

			if (userId) {
				const likedClubRes = await axios.get("/likedClubs/ids", {
					params: {
						userId: userId,
					},
				});
				setLikedClubs(likedClubRes.data.likedClubIdList);
			}
		} catch (err) {
			console.log(err);
		}
	};

	const handleLikedClubs = (clubId) => {
		let index = likedClubs.indexOf(clubId);

		try {
			if (likedClubs.includes(clubId)) {
				likedClubs.splice(index, 1);
				setLikedClubs([...likedClubs]);
				handleLikeDelete(clubId);
			} else {
				setLikedClubs([...likedClubs, clubId]);
				handleLikePost(clubId);
			}
		} catch (err) {
			console.log(err);
		} finally {
			fetchData();
		}
	};

	const handleLikePost = async (clubId) => {
		try {
			await axios.post("/likedClubs", {
				clubId: Number(clubId),
				userId: userId,
			});
		} catch (err) {
			message.error("  .");
		}
	};

	const handleLikeDelete = async (clubId) => {
		try {
			axios.delete("/likedClubs", {
				params: { userId: userId, clubId: Number(clubId) },
			});
		} catch (err) {
			console.log(err);
		}
	};

	return (
		<Wrapper>
			{loading ? (
				<SpinContainer>
					<Spin />
				</SpinContainer>
			) : (
				<>
					<MainTitle onClick={() => window.location.reload()}>
						 
					</MainTitle>
					<SearchBar keyword={keyword} setKeyword={setKeyword} />
					<TagFilter
						selectedTags={selectedTags}
						setSelectedTags={setSelectedTags}
					/>
					<TitleRow>
						<Title>{total} </Title>
						<CheckboxFilter
							onChange={(e) => {
								setClubStatus(e.target.checked ? "ACTIVE" : "");
							}}
						>
							
						</CheckboxFilter>
						<SortFilter
							showSearch
							placeholder=""
							onChange={(value) => setSortBy(value)}
						>
							<Option value="createdAt"></Option>
							<Option value="likes"></Option>
						</SortFilter>
					</TitleRow>
					<Row gutter={[90, 70]}>
						{clubs
							? clubs.map((club) => (
									<Col key={club.id}>
										<ClubCard
											userId={userId}
											club={club}
											likedClubs={likedClubs}
											handleLikedClubs={handleLikedClubs}
										/>
									</Col>
							  ))
							: ""}
					</Row>
					<PaginationRow>
						<Pagination
							total={total}
							pageSize={9}
							current={page}
							onChange={(page) => setPage(page)}
						/>
					</PaginationRow>
				</>
			)}
		</Wrapper>
	);
};

export default Main;

const { Option } = Select;

const Wrapper = styled.section`
	width: 1200px;
	margin: 90px auto;

	${customMedia.lessThan("desktop")`
  width: 880px;
  `}
`;

const MainTitle = styled.div`
	font-size: 26px;
	font-weight: bold;
	text-align: center;
	cursor: pointer;

	${customMedia.lessThan("desktop")`
    font-size: 24px;
  `}
`;

const TitleRow = styled.div`
	display: flex;
	align-items: center;
	margin: 50px 0;
`;

const Title = styled.div`
	font-weight: 500;
	font-size: 24px;
	flex: 5;

	${customMedia.lessThan("desktop")`
  font-size: 20px;
  `}
`;

const CheckboxFilter = styled(Checkbox)`
	font-size: 18px;
	flex: 0.7;

	${customMedia.lessThan("desktop")`
    font-size: 16px;
  `}

	.ant-checkbox-inner {
		width: 20px;
		height: 20px;

		${customMedia.lessThan("desktop")`
      width: 16px;
      height: 16px;
  `}
	}

	.ant-checkbox-checked .ant-checkbox-inner {
		background-color: #fea82f;
		border-color: #fea82f;
	}

	.ant-checkbox-checked::after {
		border-color: #fea82f;
	}

	.ant-checkbox-wrapper:hover .ant-checkbox-inner,
	.ant-checkbox:hover .ant-checkbox-inner,
	.ant-checkbox-input:focus + .ant-checkbox-inner {
		border-color: #fea82f;
	}
`;

const SortFilter = styled(Select)`
	flex: 0.7;

	.ant-select-selection-item {
		font-size: 16px;

		${customMedia.lessThan("desktop")`
      font-size: 14px;
  `}
	}

	&:not(.ant-select-disabled):hover .ant-select-selector {
		border-color: #fea82f;
	}
`;

const PaginationRow = styled(Row)`
	width: 100%;
	margin-top: 90px;
	justify-content: center;
`;

const SpinContainer = styled.div`
	width: 100%;
	height: 80vh;

	display: flex;
	justify-content: center;
	align-items: center;
`;
